.hero {
  padding: 120px 0 80px;
  background: #000000;
  color: white;
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 80%, rgba(255, 215, 0, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 215, 0, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.05) 0%, transparent 50%);
  z-index: 1;
}

.hero::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background:
    linear-gradient(45deg, transparent 30%, rgba(255, 215, 0, 0.1) 50%, transparent 70%),
    linear-gradient(-45deg, transparent 30%, rgba(255, 255, 255, 0.05) 50%, transparent 70%);
  animation: shimmer 3s ease-in-out infinite;
  z-index: 2;
}

@keyframes shimmer {
  0%, 100% {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    opacity: 0.5;
  }
  50% {
    transform: translateX(-30%) translateY(-30%) rotate(1deg);
    opacity: 1;
  }
}

.hero .container {
  position: relative;
  z-index: 3;
}

.hero .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  line-height: 1.2;
  font-family: 'Playfair Display', serif;
  background: linear-gradient(45deg, #ffffff, #ffd700, #ffffff);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: goldShimmer 4s ease-in-out infinite;
  text-shadow: 0 0 30px rgba(255, 215, 0, 0.3);
}

@keyframes goldShimmer {
  0%, 100% {
    background-position: 0% 50%;
    filter: brightness(1);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.2);
  }
}

.highlight {
  background: linear-gradient(45deg, #ffd700, #ffed4e, #ffd700);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  position: relative;
  animation: goldPulse 2s ease-in-out infinite;
}

@keyframes goldPulse {
  0%, 100% {
    filter: brightness(1) drop-shadow(0 0 10px rgba(255, 215, 0, 0.5));
  }
  50% {
    filter: brightness(1.3) drop-shadow(0 0 20px rgba(255, 215, 0, 0.8));
  }
}

.highlight::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #ffd700, transparent);
  border-radius: 2px;
  animation: goldLine 3s ease-in-out infinite;
}

@keyframes goldLine {
  0%, 100% {
    opacity: 0.5;
    transform: scaleX(0.8);
  }
  50% {
    opacity: 1;
    transform: scaleX(1);
  }
}

.hero-subtitle {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  opacity: 0.95;
  font-family: 'Poppins', sans-serif;
  color: #ffffff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.hero-description {
  font-size: 1.1rem;
  margin-bottom: 2.5rem;
  opacity: 0.9;
  line-height: 1.7;
  font-family: 'Poppins', sans-serif;
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Hero Button Specific Styles */
.btn-hero {
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.btn-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.4), transparent);
  transition: left 0.6s ease;
}

.btn-hero:hover::before {
  left: 100%;
}

.btn-gold {
  background: linear-gradient(135deg, #ffd700, #ffed4e) !important;
  color: #000000 !important;
  border: 2px solid #ffd700 !important;
  font-weight: 700 !important;
  box-shadow: 0 4px 20px rgba(255, 215, 0, 0.4) !important;
}

.btn-gold:hover {
  background: linear-gradient(135deg, #ffed4e, #ffd700) !important;
  box-shadow: 0 6px 30px rgba(255, 215, 0, 0.6) !important;
  transform: translateY(-3px) !important;
}

.btn-black {
  background: #000000 !important;
  color: #ffffff !important;
  border: 2px solid #ffd700 !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5) !important;
}

.btn-black:hover {
  background: #000000 !important;
  color: #ffd700 !important;
  border-color: #ffd700 !important;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.8), 0 0 20px rgba(255, 215, 0, 0.4) !important;
  transform: translateY(-3px) !important;
}

.btn-black-outline {
  background: transparent !important;
  color: #ffffff !important;
  border: 2px solid #ffd700 !important;
  box-shadow: none !important;
}

.btn-black-outline:hover {
  background: rgba(0, 0, 0, 0.8) !important;
  color: #ffd700 !important;
  border-color: #ffd700 !important;
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.3) !important;
  transform: translateY(-2px) !important;
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-avatar {
  width: auto;
  height: auto;
  max-width: min(400px, 35vw);
  transform: scale(0.85);
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid rgba(255, 215, 0, 0.6);
  box-shadow:
    0 0 40px rgba(255, 215, 0, 0.3),
    0 0 80px rgba(255, 215, 0, 0.1),
    inset 0 0 20px rgba(255, 255, 255, 0.1);
  position: relative;
  animation: luxuryFloat 8s ease-in-out infinite;
  overflow: visible;
}

@keyframes luxuryFloat {
  0%, 100% { transform: translateY(0px) scale(1); }
  50% { transform: translateY(-5px) scale(1.02); }
}


.avatar-img {
  width: auto;
  height: auto;
  max-width: 100%;
  border-radius: 12px;
  border: 2px solid rgba(255, 215, 0, 0.4);
  position: relative;
  z-index: 1;
  filter: contrast(1.05) brightness(1.02);
  background-color: rgba(0, 0, 0, 0.3);
  display: block;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .hero .container {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 2rem;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.25rem;
  }
  
  .hero-buttons {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .hero-avatar {
    width: auto;
    height: auto;
    max-width: min(200px, 70vw);
    transform: scale(0.9);
  }
  
  }